import * as React from "react";
import Transition from "src/packages/transition/Transition";
import Unfold from "src/packages/transition/Unfold";
import Button from "src/packages/button/Button";
import Code from "src/components/Code";
import CodeBox from "src/components/CodeBox";
import Space from "src/components/Space";
import { renderString } from "src/utils/renderCode";

class TransitionExample extends React.Component {
  public static displayName = "TransitionExample";

  public state = {
    visible1: true,
    visible2: true,
    visible3: true
  };

  public render() {
    return (
      <div className="transition-example">
        <section>
          <h1>Transition </h1>
          <p className="text">/</p>
        </section>
        <section>
          <h2></h2>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
        </section>
        <div className="example">
          <div
            className="container"
            style={{ height: "300px", alignItems: "flex-start" }}
          >
            <Button
              type="primary"
              style={{ marginRight: "20px" }}
              onClick={() => this.setState({ visible1: !this.state.visible1 })}
            >
              Toggle Visible
            </Button>
            <Transition
              visible={this.state.visible1}
              beforeEnter={{ opacity: 0 }}
              afterEnter={{ opacity: 1 }}
              transitionActive={{
                transition: "1s all cubic-bezier(.645, .045, .355, 1)"
              }}
            >
              <div
                style={{
                  width: "150px",
                  height: "150px",
                  backgroundColor: "rgb(52, 70, 110)",
                  fontSize: "24px",
                  fontWeight: 600,
                  textAlign: "center",
                  lineHeight: "150px",
                  color: "rgba(255,255,255,0.85)"
                }}
              >
                Transition
              </div>
            </Transition>
          </div>
          <CodeBox title="" description="">
            {renderString(`import { Transition } from 'xue-react'`)}
            <br />
            <Code
              code={`&lt;Transition visible={this.state.visible1} beforeEnter={beforeEnterCSS} afterEnter={afterEnterCSS}&gt;`}
            />
            <Space count={4} />
            <span className="label">&lt;</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <span className="htmlstr">Transition</span>
            <span className="label">&lt;</span>
            <span className="label">/</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <br />
            <Code code={`&lt;/Transition&gt;`} />
          </CodeBox>
        </div>
        <div className="example">
          <div
            className="container"
            style={{ height: "300px", alignItems: "flex-start" }}
          >
            <Button
              type="primary"
              style={{ marginRight: "20px" }}
              onClick={() => this.setState({ visible2: !this.state.visible2 })}
            >
              Toggle Visible
            </Button>
            <Unfold visible={this.state.visible2}>
              <div
                style={{
                  width: "500px",
                  height: "150px",
                  backgroundColor: "rgb(52, 70, 110)",
                  fontSize: "24px",
                  fontWeight: 600,
                  textAlign: "center",
                  lineHeight: "150px",
                  color: "#c41d7f"
                }}
              >
                Horizontal
              </div>
            </Unfold>
          </div>
          <CodeBox
            title="/"
            description="Unfold  Transition //"
          >
            {renderString(`import { Unfold } from 'xue-react'`)}
            <br />
            <Code code={`&lt;Unfold visible={this.state.visible2}&gt;`} />
            <Space count={4} />
            <span className="label">&lt;</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <span className="htmlstr">Horizontal</span>
            <span className="label">&lt;</span>
            <span className="label">/</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <br />
            <Code code={`&lt;/Unfold&gt;`} />
          </CodeBox>
        </div>
        <div className="example">
          <div
            className="container"
            style={{ height: "300px", alignItems: "flex-start" }}
          >
            <Button
              type="primary"
              style={{ marginRight: "20px" }}
              onClick={() => this.setState({ visible3: !this.state.visible3 })}
            >
              Toggle Visible
            </Button>
            <Unfold visible={this.state.visible3} vertical={true}>
              <div
                style={{
                  width: "500px",
                  height: "150px",
                  backgroundColor: "rgb(52, 70, 110)",
                  fontSize: "24px",
                  fontWeight: 600,
                  textAlign: "center",
                  lineHeight: "150px",
                  color: "#c41d7f"
                }}
              >
                Vertical
              </div>
            </Unfold>
          </div>
          <CodeBox title="/" description="/">
            {renderString(`import { Unfold } from 'xue-react'`)}
            <br />
            <Code
              code={`&lt;Unfold visible={this.state.visible3} vertical={true}&gt;`}
            />
            <Space count={4} />
            <span className="label">&lt;</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <span className="htmlstr">Vertical</span>
            <span className="label">&lt;</span>
            <span className="label">/</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <br />
            <Code code={`&lt;/Unfold&gt;`} />
          </CodeBox>
        </div>
        <section>
          <h2>API</h2>
        </section>
        <section className="sub">
          <h3>Transition</h3>
        </section>
        <table className="api-table">
          <thead>
            <tr>
              <th></th>
              <th className="des"></th>
              <th className="type"></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>visible</td>
              <td className="des"></td>
              <td className="type">boolean</td>
              <td></td>
            </tr>
            <tr>
              <td>beforeEnter</td>
              <td className="des"></td>
              <td className="type">React.CSSProperties</td>
              <td></td>
            </tr>
            <tr>
              <td>afterEnter</td>
              <td className="des"></td>
              <td className="type">React.CSSProperties</td>
              <td></td>
            </tr>
            <tr>
              <td>beforeLeave</td>
              <td className="des"></td>
              <td className="type">React.CSSProperties</td>
              <td></td>
            </tr>
            <tr>
              <td>afterLeave</td>
              <td className="des"></td>
              <td className="type">React.CSSProperties</td>
              <td></td>
            </tr>
            <tr>
              <td>transitionActive</td>
              <td className="des">/</td>
              <td className="type">React.CSSProperties</td>
              <td>{`{ transition: '.3s all cubic-bezier(.645, .045, .355, 1)' }`}</td>
            </tr>
          </tbody>
        </table>
        <section className="sub">
          <h3>Unfold</h3>
        </section>
        <table className="api-table">
          <thead>
            <tr>
              <th></th>
              <th className="des"></th>
              <th className="type"></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>visible</td>
              <td className="des"></td>
              <td className="type">boolean</td>
              <td></td>
            </tr>
            <tr>
              <td>vertical</td>
              <td className="des">/</td>
              <td className="type">boolean</td>
              <td>false</td>
            </tr>
            <tr>
              <td>transitionTime</td>
              <td className="des">/</td>
              <td className="type">number</td>
              <td>0.3</td>
            </tr>
          </tbody>
        </table>
        <section>
          <h2>Tips</h2>
          <p className="text">
            Transition  beforeEnterafterEnterbeforeLeaveafterLeave
            //
            beforeEnter | afterLeave beforeLeave |
            afterEnter
          </p>
        </section>
      </div>
    );
  }
}

export default TransitionExample;



